import $ from 'jquery';
import plugin from './plugin';

class Tiles {
  constructor(element, options) {
    const $element = $(element);
    const $tile = $element.find('.tiles__wrapper');

    $tile.mouseover((evt) => {
      $(evt.target).siblings().addClass('not-hovered');
    }).mouseleave(() => {
      $tile.removeClass('not-hovered');
    });
  }
}

Tiles.DEFAULTS = {};

plugin('Tiles', Tiles);
